<template>
  <div class="fashion">
  	<div class="fashion-div1">
  		<h2 class="fashion-div1-h2"></h2>
  	</div>
  	<div v-for="obj in fashion1">
  		<img :data-src="obj.imgUrl"/>
  	</div>
  	<cationfixed></cationfixed>
  </div>
</template>

<script lang="babel">
import cationfixed from "./common/cationfixed";
export default {
	data(){//
		return{
			fashion1:[]
		}	
	},
	methods:{//js
		
	},
	components:{//
		cationfixed
	},
	mounted(){//ajax
		console.log(1)
			var _this=this;
			_this.$http({
				method: 'get', //ajax 
				url: "./static/json/fashion3.json", //()
				params: {} //data 
			}).then(function(data) {
				//data;success //sliderArr,sliderArr
				_this.fashion1 = data.data; //_this.xxxArr=data.body;
			}, function(error) {
				//
				alert("");
			})
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss' >
		.fashion{
			width: 20em;
			overflow-y: scroll;
			margin: 0 auto;
			background: #fff;
		}
		.fashion-div1{
			padding: 0.5em;
			
		}
		.fashion-div1-h2{
		font-weight: 600;
    font-size: 1.6em;
    margin: 1.5em 0 .4em;
    font-family: proxima-nova, sans-serif;
    line-height: 1.2;
    }
</style>


